.sidebar {
    width: 10%;
    height: 100%;
    background-color: #f2f2f2;
    position: fixed;
    top: 0;
    left: 0;
    overflow-y: auto;
    padding: 20px;
  }
  
  .sidebar ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .sidebar li {
    margin-bottom: 10px;
  }
  
  .sidebar a {
    display: block;
    padding: 10px;
    text-decoration: none;
    color: black;
    background-color: #eee;
  }
  
  .sidebar a:hover {
    background-color: #ddd;
  }
  
  .sidebar .active {
    background-color: #007bff;
    color: white;
  }